import React, { useState } from "react";
import Video from "../../assets/Canon EOS R and RF Lenses First Look at Full Frame Mirrorless Camera-4 1080p (Cutted).mp4"
import "./style.scss";
import VisibilitySensor from 'react-visibility-sensor';
import ReactPlayer from 'react-player';


function Index() {
  const [playing, setPlaying] = useState(false)
  return (

    <section className="section-video" data-scroll-section>
      <VisibilitySensor partialVisibility={true} onChange={(isVisible) => {
        if (isVisible) {
          setPlaying(true);
          console.log("visible")
        } else {
          setPlaying(false)
          console.log("not visible")

        }
      }}>
        <div>
          <ReactPlayer url={Video} playing={playing} controls={false} width="100%" height="100%" loop={true} />
        </div>
      </VisibilitySensor>
    </section>

  );
}

export default Index;
